import * as React from 'react';
import {types} from 'mobx-state-tree';
import {observer} from 'mobx-react-lite';

const Category = types.model({
  id: types.identifier,
  name: types.string
});
const Product = types.model({
  id: types.identifier,
  category: types.reference(Category),
  name: types.string
});
const RootStore = types.model('RootStore', {
  categorys: types.array(Category),
  products: types.array(Product)
});
// Instantiate a couple model instances
const categoryA = Category.create({
  id: 'c1',
  name: '类型1'
});
const productA = Product.create({
  id: 'p1',
  category: categoryA.id,
  name: 'Holmgren'
});

// Now instantiate the store!
const rootStore = RootStore.create({
  categorys: [ categoryA ],
  products: [ productA as any ]
});

const DemoComponent = observer((props) => {
  return <div>Hello, {rootStore.products[ 0 ].category.id}!</div>;
});

export default DemoComponent;
